nter-events: none;\n        user-select: none;\n        z-index: 1;\n        cursor: pointer;\n        transition: all 0.15s ease-in-out;\n      }\n    }\n    input {\n      display: none;\n      &:checked {\n        @for $i from 1 through $number-of-options {\n          &:nth-child(#{$i * 2 - 1}) ~ .debtAmountPos {\n            left: #{($i * $col-width) - ($col-width * 0.5)};\n          }\n        }\n      }\n    }\n    .debtAmountPos {\n      display: block;\n      position: absolute;\n      left: #{(1 * $col-width) - ($col-width * 0.5)};\n      top: 50%;\n      transition: all 0.15s ease-in-out;\n      transform: translate(-50%, -50%);\n      opacity: 1;\n      z-index: 1;\n      width: 80px;\n      height: 80px;\n      border-radius: 50%;\n      background: radial-gradient(\n        50% 50% at 50% 50%,\n        #ffffff 0%,\n        rgba(255, 255, 255, 0) 60.42%\n      );\n      box-shadow: 0px 0px 6px 1px rgba(255, 255, 255, 0.5);\n    }\n\n    .debtAmountPosToken {\n      position: absolute;\n      width: 100%;\n      bottom: 50%;\n      background-color: transparent;\n      top: 50%;\n      left: 0;\n      &A {\n        // box-shadow: 0px 0px 2px 1px #f00;\n        // transform: rotate(26deg);\n        transition: 1s;\n      }\n\n      &B {\n        // box-shadow: 0px 0px 2px 1px #00d9ff;\n        // transform: rotate(-26deg);\n        transition: 1s;\n      }\n\n      &ObjA {\n        @include icon-token();\n        transition: 1s;\n      }\n      &ObjB {\n        @include icon-token();\n        transition: 1s;\n      }\n    }\n\n    .trackMark {\n      display: inline-block;\n      position: relative;\n      width: 20%;\n      height: 100%;\n      user-select: none;\n      cursor: pointer;\n\n      &Gradient {\n        content: '';\n        background: $linear-gradient-mark;\n        position: absolute;\n        height: 70px;\n        width: 3px;\n        left: 50%;\n        top: -11px;\n\n        &left {\n          @include transform-mark-gradient(1);\n        }\n        &center {\n          background: transparent;\n        }\n        &right {\n          @include transform-mark-gradient(-1);\n        }\n      }\n      &Label {\n        position: absolute;\n        font-size: 12px;\n        line-height: 14px;\n        color: #777777;\n        width: 24px;\n        bottom: 6px;\n        left: 50%;\n        margin-right: -50%;\n        transform: translate(-50%, 0px);\n\n        &ColorBlue {\n          color: #1fcbff;\n        }\n      }\n    }\n  }\n}\n\n.buttonIcon {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  border: none;\n  cursor: pointer;\n  background: transparent;\n  position: absolute;\n  top: 50%;\n  left: 50%;\n  transform: translate(-50%, -50%);\n\n  &:hover {\n    opacity: 0.7;\n  }\n\n  img {\n    width: 40px;\n    height: 55px;\n    object-fit: cover;\n  }\n}\n"],"sourceRoot":""}]);
