      @include linear-gradient-gray-el;\n        @include linear-gradient-green(270deg);\n        left: 0;\n      }\n      &::after {\n        @include linear-gradient-gray-el;\n        @include linear-gradient-green(90deg);\n        right: 0;\n      }\n    }\n\n    .slide.active {\n      cursor: pointer;\n      color: $color-white !important;\n      &::before {\n        @include linear-gradient-gray-el;\n        @include linear-gradient-gray(270deg);\n        left: 0;\n      }\n      &::after {\n        @include linear-gradient-gray-el;\n        @include linear-gradient-gray(90deg);\n        right: 0;\n      }\n    }\n\n    .slide.left {\n      cursor: pointer;\n      color: $color-green;\n      //  margin-left: 10px;\n      &::before {\n        @include init-pseudo-el-gren-default;\n        @include linear-gradient-green(90deg);\n      }\n\n      &::after {\n        content: '';\n        background: #000;\n        width: 10px;\n        left: -5px;\n        z-index: 1;\n        opacity: 1;\n        bottom: 0;\n        position: absolute;\n        height: 100%;\n      }\n    }\n    .slide.right {\n      // margin-right: 10px;\n      cursor: pointer;\n      &::before {\n        @include init-pseudo-el-gren-default;\n        @include linear-gradient-green(270deg);\n      }\n      &::after {\n        content: '';\n        background: #000;\n        width: 10px;\n        right: -5px;\n        z-index: 1;\n        opacity: 1;\n        bottom: 0;\n        position: absolute;\n        height: 100%;\n      }\n      color: $color-green;\n    }\n\n    .containerContent {\n      display: flex;\n      flex-direction: column;\n      align-items: center;\n      justify-content: center;\n      position: relative;\n      font-size: 16px;\n      text-transform: lowercase;\n    }\n\n    .lamp.active {\n      &::before {\n        content: '';\n        position: absolute;\n        top: 0;\n        bottom: 0;\n        left: 0;\n        @include light-lamp(\n          $color-white,\n    