J,qCACE,cAAA,CACA,gBAAA,CACA,0BAAA,CAIA,2CACE,UAAA","sourcesContent":[".wrapper {\n}\n\n.header {\n  gap: 40px;\n  display: flex;\n  justify-content: center;\n  // margin-top: 20px;\n  flex-wrap: wrap;\n\n  // > * {\n  // flex: 1;\n  // }\n\n  @media (max-width: 480px) {\n    display: grid;\n\n    padding: 0 50px;\n    gap: 15px;\n    // margin-top: -3px;\n    z-index: 1;\n\n    grid-template-columns: repeat(2, 1fr);\n\n    > :nth-child(2) {\n      order: 1;\n      grid-column: span 2;\n    }\n  }\n}\n\n.keywordBtn {\n  color: var(--blue-light);\n  font-size: 20px;\n  padding: 3px 15px;\n  text-align: center;\n  opacity: 0.7;\n\n  border-radius: 15px;\n  border: 1px solid rgba(31, 203, 255, 0.2);\n\n  &:hover {\n    color: var(--blue-light);\n    opacity: 1;\n  }\n}\n\n.content {\n  margin: 20px auto 0;\n  // width: 70vw;\n  // height: 100%;\n  // min-height: 70vh;\n\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n\n  position: relative;\n\n  .info {\n    width: 100%;\n    z-index: 1;\n\n    &::before {\n      content: '';\n      position: absolute;\n\n      margin-top: auto;\n      height: 50%;\n      z-index: -1;\n      filter: blur(20px);\n      background: linear-gradient(\n        90deg,\n        rgba(0, 0, 0, 0) 3.92%,\n        rgba(118, 255, 3, 0.2) 43.95%,\n        rgba(118, 255, 3, 0.2) 55.96%,\n        rgba(0, 0, 0, 0) 100%\n      );\n\n      @keyframes identifier {\n        from {\n          left: 0;\n          right: 0;\n        }\n\n        to {\n          left: 100px;\n          right: 100px;\n        }\n      }\n\n      animation: identifier 3s infinite alternate;\n    }\n\n    gap: 0 40px;\n\n    .imgWrapper {\n      position: relative;\n      border-radius: 50%;\n\n      &::after {\n        // content: '';\n        position: absolute;\n        border-radius: 50%;\n\n        left: 0;\n        right: 0;\n        height: 100%;\n      }\n    }\n\n    img {\n      max-width: 100%;\n      box-shadow: 0px 0px 15px -2px #76ff03;\n      border-radius: 50%;\n      border: 1px solid #fff;\n\n      // @keyframes anim {\n      //   from {\n      //     box-shadow: 0px 0px 15px 2px #76ff03;\n      //   }\n\n      //   to {\n      //     box-shadow: 0px 0px 0px 2px #76ff03;\n      //   }\n      // }\n\n      // animation: anim 8s infinite alternate;\n    }\n\n    > * {\n      font-size: 30px;\n    }\n\n    display: grid;\n    grid-template-columns: repeat(3, minmax(0, 33%));\n    align-items: center;\n    position: relative;\n\n    h2,\n    h4,\n    span {\n      font-weight: 400;\n      text-shadow: 0px 0px 15px #ffd900;\n    }\n\n    h2 {\n      text-align: right;\n\n      span {\n        color: #ffd900;\n      }\n    }\n\n    .particles {\n      span:first-of-type {\n        color: var(--green-light);\n      }\n    }\n  }\n\n  @media (max-width: 580px) {\n    .info {\n      display: unset;\n      margin-top: -20px;\n      h2 {\n        position: relative;\n        text-shadow: 0px 0px 15px #ffd900;\n\n        // &::before {\n        //   content: '';\n        //   position: absolute;\n        //   left: -30%;\n        //   right: -30%;\n\n        //   margin-top: auto;\n        //   height: 100%;\n        //   z-index: -1;\n        //   filter: blur(20px);\n        //   background: linear-gradient(\n        //     90deg,\n        //     rgba(0, 0, 0, 0) 3.92%,\n        //     rgba(118, 255, 3, 0.2) 43.95%,\n        //     rgba(118, 255, 3, 0.2) 55.96%,\n        //     rgba(0, 0, 0, 0) 100%\n        //   );\n        // }\n        // left: -20px;\n      }\n      &::before {\n        content: unset;\n      }\n\n      .imgWrapper {\n        position: absolute;\n        right: -105%;\n\n        img {\n          // box-shadow: 0px 0px 30px -1px #76ff03;\n\n          box-shadow: 0px 0px 25px -10px #76ff03, 0px 0px 200px -20px #76ff03;\n        }\n        top: 45px;\n      }\n\n      .particles {\n        display: none;\n      }\n    }\n\n    .advantages {\n      // text-align: left;\n      margin-top: 20px;\n      position: relative;\n      left: -25px;\n      display: flex;\n      flex-direction: column;\n    }\n  }\n}\n\n.advantages {\n  display: grid;\n  width: 62vw;\n  list-style-type: none;\n  margin: -30px auto 0;\n  gap: 20px 0;\n\n  grid-template-columns: repeat(3, minmax(0, 33%));\n  grid-template-rows: repeat(2, 1fr);\n  text-align: center;\n  justify-items: center;\n\n  li {\n    max-width: 200px;\n  }\n\n  li:nth-child(1) {\n    grid-column: 1 / 2;\n    grid-row: 1;\n  }\n\n  li:nth-child(2) {\n    grid-column: 2 /3;\n    grid-row: 2;\n  }\n\n  li:nth-child(3) {\n    grid-column: 3 / 4;\n    grid-row: 1;\n  }\n\n  h6,\n  p {\n    font-weight: 400;\n  }\n\n  h6 {\n    color: var(--primary-color);\n    text-shadow: 0 0 5px var(--primary-color);\n    font-size: 20px;\n    position: relative;\n    z-index: 1;\n\n    &::before {\n      content: '';\n      z-index: -1;\n      position: absolute;\n      left: -20%;\n      right: -20%;\n      height: 100%;\n      filter: blur(7.5px);\n      background: linear-gradient(\n        90deg,\n        rgba(0, 0, 0, 0) 3.92%,\n        rgba(54, 214, 174, 0.15) 46.46%,\n        rgba(54, 214, 174, 0.15) 56.46%,\n        rgba(0, 0, 0, 0) 100%\n      );\n    }\n  }\n\n  p {\n    margin-top: 7px;\n    line-height: 23px;\n    color: rgba(255, 255, 255, 0.5);\n  }\n\n  a {\n    &:hover {\n      opacity: 0.7;\n    }\n  }\n}\n"],"sourceRoot":""}]);
