ient-gray(270deg);\n        left: 0;\n      }\n\n      &::after {\n        @include linear-gradient-gray-el;\n        @include linear-gradient-gray(90deg);\n        right: 0;\n      }\n    }\n\n    .slide.left {\n      cursor: pointer;\n      color: $color-green;\n\n      &::before {\n        @include init-pseudo-el-gren-default;\n        @include linear-gradient-green(90deg);\n      }\n    }\n\n    .slide.right {\n      cursor: pointer;\n\n      &::before {\n        @include init-pseudo-el-gren-default;\n        @include linear-gradient-green(270deg);\n      }\n\n      color: $color-green;\n    }\n\n    .containerContent {\n      display: flex;\n      flex-direction: column;\n      align-items: center;\n      justify-content: center;\n      position: relative;\n      font-size: 16px;\n    }\n\n    .lamp.active {\n      &::before {\n        content: '';\n        position: absolute;\n        top: 0;\n        bottom: 0;\n        left: 0;\n        @include light-lamp($color-white,\n          $color-border-lamp-white,\n          $color-shadow-lamp-white );\n        border-radius: 0px 1px 1px 0px;\n      }\n\n      &::after {\n        content: '';\n        position: absolute;\n        top: 0;\n        bottom: 0;\n        right: 0;\n        @include light-lamp($color-white,\n          $color-border-lamp-white,\n          $color-shadow-lamp-white );\n        border-radius: 1px 0px 0px 1px;\n      }\n    }\n\n    .lamp.left {\n      &::before {\n        content: '';\n        position: absolute;\n        top: 50%;\n        left: 6px;\n        transform: translate(6px, -50%);\n        @include pils;\n      }\n\n      &::after {\n        content: '';\n        position: absolute;\n        top: 0;\n        bottom: 0;\n        right: 0;\n        @include light-lamp($color-white,\n          $color-border-lamp-green,\n          $color-shadow-lamp-green );\n        border-radius: 1px 0px 0px 1px;\n      }\n    }\n\n    .lamp.right {\n      &::before {\n        content: '';\n        position: absolute;\n        top: 0;\n        bottom: 0;\n        left: 0;\n        @include light-lamp($color-white,\n          $color-border-lamp-green,\n          $color-shadow-lamp-green );\n        border-radius: 0px 1px 1px 0px;\n      }\n\n      &::after {\n        content: '';\n        position: absolute;\n        top: 50%;\n        right: 6px;\n        transform: translate(-6px, -50%);\n        @include pils;\n      }\n    }\n  }\n}\n\n.transition {\n  transition: ease all 0.5s;\n}"],"sourceRoot":""}]);
